import React, { useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import AdminLayout from '@components/admin/Layout';

import fetch from 'cross-fetch';
import FormControl from '@mui/material/FormControl';
import Input from '@mui/material/Input';

const PartnersIndexPage = () => {
  const [partnerData, setPartnerData] = useState({
    lastNameKanji: '',
    firstNameKanji: '',
    lastName: '',
    firstName: '',
    phone: '',
    email: ''
  });

  useEffect(() => {
    const f = async () => {
      try {
        const res = await fetch(
          `${process.env.API_URL}/api/partners/ea4a1c2a-58ff-4b93-9f6c-36f96436381d`
        );

        // @TODO 404

        if (res.status >= 400) {
          throw new Error('Bad response [GET] /api/partners/');
        }

        setPartnerData(await res.json());
      } catch (e) {
        console.log(e);
      }
    };

    f();
  }, []);

  const UploadPhoto = () => {
    const [img, setImg] = useState('');
    const [loading, setLoading] = useState(false);

    return (
      <Box mb={5}>
        <Box
          position='relative'
          border='1px solid #333'
          width={300}
          height={300}
          display='flex'
          justifyContent='center'
          alignItems='center'
        >
          <Box position='absolute' zIndex={2}>
            {loading && <>画像更新中...</>}
          </Box>
          <Box position='absolute'>
            <Box
              width={300}
              height={300}
              sx={{
                backgroundPosition: 'center center',
                backgroundRepeat: 'no-repeat',
                backgroundImage: `url(${img})`,
                backgroundSize: 'cover'
              }}
            />
          </Box>
        </Box>
        <FormControl>
          <Input
            type='file'
            onChange={e => {
              // ファイル未指定やキャンセル時
              if (!e.target.value) {
                return;
              }

              setImg('');
              setLoading(true);

              console.log('[File]');
              const file = e.target.files[0];

              console.log(file);

              const reader = new FileReader();

              reader.onload = e => {
                console.log('onload reader');
                console.log(e);
                console.log(e.target.result);
                setImg(e.target.result);

                const image = new Image();
                image.onload = () => {
                  setLoading(false);
                };
                image.src = e.target.result;
              };

              reader.readAsDataURL(file);
            }}
          />
        </FormControl>
      </Box>
    );
  };

  return (
    <AdminLayout>
      <Typography variant='h6'>DARUMA ADMIN TOOLS</Typography>
      <Typography>画像アップロード *TODO</Typography>
      {/*
      {partnerData.lastName} {partnerData.firstName}
      {[1, 2, 3].map((_, i) => (
        <UploadPhoto key={i} />
      ))}
        */}
    </AdminLayout>
  );
};

export default PartnersIndexPage;
